.home {
  padding: 20px;
}

.fit_content {
  max-width: 300px;
}

.search_form {
  margin-bottom: 20px;
}

.search_form input {
  padding: 10px;
  font-size: 16px;
  margin-right: 10px;
}

.search_form button {
  padding: 10px 20px;
  font-size: 16px;
}

.books_container {
  display: flex;
  flex-direction: column;
}

.tag_section {
  margin-bottom: 20px;
}

.tag_section h2 {
  margin-bottom: 10px;
  font-size: 24px;
  color: #333;
}

.books_list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: scroll;
}

.books_list > * {
  flex: 1 1 calc(20% - 30px); /* Ajuste conforme necessÃ¡rio */
  max-width: 200px;
  min-width: 200px;
}

.react-multi-carousel-track {
  gap: 20px;
}

.react-multi-carousel-item {
  max-width: fit-content !important;
}

