.login-page {
  .login {
    display: flex;
    align-items: center;
    height: 100vh;
    background-color: white;
  }

  .left-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 450px;
  }

  .logo-container {
    background: #032924;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 15px;
  }

  .left-content h1 {
    font-weight: 700;
    font-size: 20px;
    color: black;
  }

  .left-content p {
    font-weight: 500;
    font-size: 18px;
    color: black;
    margin-bottom: 30px;
  }

  .left-content form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 90%;
  }

  .left-content label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .left-content span {
    color: #555555;
  }

  .input-and-image input {
    height: 50px;
    width: 100%;
    background-color: #f1f3f6;
    border-radius: 10px;
    padding-left: 20px;
    margin-bottom: 20px;
  }

  .input-and-image input:focus-visible {
    outline: none;
    box-shadow: none;
  }

  .input-and-image {
    position: relative;
  }

  .image-input {
    background-color: #04332d;
    position: absolute;
    top: 0%;
    right: 0px;
    width: 50px;
    height: 50px;
    border-radius: 10px;
  }

  .image-input img {
    margin: 13px auto;
  }

  .btn-login {
    background-color: #04332d;
    color: white;
    font-weight: 700;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    margin-top: 10px;
    transition: 0.3s ease-in-out;
  }

  .btn-login:hover {
    background-color: #032924;
  }

  .right-content {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    background-color: #f1f3f6;
  }
}
